const Count = (props) =>{
  const [count, setCount] = useState(0);

  const progressRef = React.useRef(() => {});
  React.useEffect(() => {
    progressRef.current = (e) => {
      if(count < props.number){
        setCount(count + 3);
      }else{
        clearInterval(e);
        setCount(props.number);
      }
    };
  });

  React.useEffect(() => {
    const timer = setInterval(() => {
      progressRef.current(timer);
      
    }, 50);
    return () => {
      clearInterval(timer);
    };
  }, []);

  return (
    <div className="single-counter">
      <div className="container">
        <div className="counter-shape">
          <div className='shape'>
            <span className="shape-1" style={{backgroundColor: props.color['A100']}}></span>
            <span className="shape-2" style={{backgroundColor: props.color['A200']}}></span>
          </div>
          <div className='shape-text'><b>{count} %</b>
            <p>{props.title}</p>
          </div>
        </div>
        <div /* class="counter-content media-body" */>
          <p>{props.children}</p>
        </div>
      </div>
    </div>
  )
}